import React, { useEffect } from "react";
const { kakao } = window;

const markerdata = [
    {
      title: " ",
      lat: 37.59976007223912,
      lng: 126.86548397026337,
    },
    {
      title: "",
      lat: 37.6015765832226956,
      lng: 126.86528488337555,
    },
    {
      title: "",
      lat: 37.59802901833839,
      lng: 126.86523742257961,
    },
  ];

export default function Map() {
  useEffect(() => {
    mapscript();
  }, []);

  const mapscript = () => {
    let container = document.getElementById("map");
    let options = {
      center: new kakao.maps.LatLng(37.60001975210991, 126.86556719830612),
      level: 3,
    };

    //map
    const map = new kakao.maps.Map(container, options);
    
    markerdata.forEach((el) => {
      //  
      new kakao.maps.Marker({
        //   
        map: map,
        //   
        position: new kakao.maps.LatLng(el.lat, el.lng),
        // hover  title
        title: el.title,
      });
    });
  };

  return <div id="map" style={{ width: "400px", height: "700px", margin: 'auto' }}></div>;
}